export const myMixin = {
  methods: {
	  // 
	  getNowDate(){
	  	return this.initDate(Date.now())
	  },
	  // 
	   getChineseTimeDiff(t1, t2) {
	    const diff = Math.abs(t1 - t2);
	    const units = [
	      { value: 86400000, name: '' }, 
	      { value: 3600000, name: '' }, 
	    ];
	  
	    let remaining = diff;
	    const result = units.map(unit => {
	      const val = Math.floor(remaining / unit.value);
	      remaining %= unit.value;
	      return { val, name: unit.name };
	    });
	    const output = result
	      .filter(item => item.val > 0)
	      .map(item => `${item.val}${item.name}`)
	      .join('');
	  
	    return output || '0';
	  },
	  /**
	   * 
	   */
	  async customizeBack() {
	  	let canNavBack = await getCurrentPages()
	  	if (canNavBack && canNavBack.length > 1) {
	  		uni.navigateBack()
	  	} else {
	  		history.back();
	  	}
	  },
    /**
     *
     * 
     * {parmars} str   utc
     * {returns} timeStr   
     */
    initTime(str) {
		
      let timestamp = new Date(str).getTime()
      var time = String(timestamp).length === 10 ? new Date(parseInt(timestamp) * 1000) : new Date(parseInt(
        timestamp))
      var y = time.getFullYear() // 
      var m = time.getMonth() + 1 // 
      if (m < 10) {
        m = '0' + m
      }
      var d = time.getDate() // 
      if (d < 10) {
        d = '0' + d
      }
      var h = time.getHours() // 
      if (h < 10) {
        h = '0' + h
      }
      var mm = time.getMinutes() // 
      if (mm < 10) {
        mm = '0' + mm
      }
      var s = time.getSeconds() // 
      if (s < 10) {
        s = '0' + s
      }
      var timeStr = y + '-' + m + '-' + d + ' ' + h + ':' + mm + ':' + s
      return timeStr
    },
	// 
	initDate(str) {
	  let timestamp = new Date(str).getTime()
	  var time = String(timestamp).length === 10 ? new Date(parseInt(timestamp) * 1000) : new Date(parseInt(
	    timestamp))
	  var y = time.getFullYear() // 
	  var m = time.getMonth() + 1 // 
	  if (m < 10) {
	    m = '0' + m
	  }
	  var d = time.getDate() // 
	  if (d < 10) {
	    d = '0' + d
	  }
	  var h = time.getHours() // 
	  if (h < 10) {
	    h = '0' + h
	  }
	  var mm = time.getMinutes() // 
	  if (mm < 10) {
	    mm = '0' + mm
	  }
	  var s = time.getSeconds() // 
	  if (s < 10) {
	    s = '0' + s
	  }
	  var timeStr = y + '-' + m + '-' + d
	  return timeStr
	}
  }
};


